* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

form {
  margin: 1em 0;
}

form input {
  width: 100%;
  padding: 1em 0;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.row img {
  max-width: 100%;
}

.row div {
  width: 100%;
  text-align: center;
  margin: 1em 0.5em;
}

@media (min-width: 1008px) {
  form input {
    width: 30%;
  }
  .row {
    justify-content: space-between;
    flex-wrap: no-wrap;
  }
  .row div {
    width: 15%;
    margin: 0.5em 1em;
  }
}
